.offers{
  width: 65%;
  /* height: 60vh; */
  display: flex;
  margin: auto;
  padding: 0px 140px;
  margin-bottom: 50px;
  /* background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%); */
  background: linear-gradient(180deg,  #24456bb6, #210722b6 80%);
  /* background: red; */
}
.offers-left{
  flex: 1;
  display: flex;
  flex-direction: column;
  /* background: red; */
  justify-content: center ;
}
.offers-left h1{
  color: #95b4b8; 
  font-size: 80px;
  font-weight: 600;
}
.offers-left p{
  color: #f5f6f7; 
  font-size: 22px;
  font-weight: 600;
}

.offers-left button{
  width:282px;
  height: 70px;
  border-radius: 35px;
  background: #ff4141;
  border: none;
  color: white;
  font-size: 22px;
  font-weight: 500;
  margin-top: 30px;
  cursor: pointer;
}
.offers-right{
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 50px;
} 
@media(max-width:1280px){
  .offers{
    padding: 0px 80px;
    margin-bottom: 120px;
  }
  .offers-left h1{
    font-size: 40px;
  }
  .offers-left p{
    font-size: 18px;
  }
  .offers-right img{
    width: 300px;
  }
  .offers-left button{
    width: 220px;
    height: 50px;
    font-size: 18px;
  }
  .offers-right{
    padding-top: 30px;
  }
}

@media(max-width:1024px){
  .offers{
    padding: 0px 60px;
    margin-bottom: 80px;
    /* height: 40vh; */
  }
  .offers-left h1{
    font-size: 30px;
  }
  .offers-left p{
    font-size: 16px;
  }
  .offers-right img{
    width: 200px;
  }
  .offers-left button{
    width: 220px;
    height: 50px;
    font-size: 18px;
  }
  .offers-right{
    padding-top: 30px;
  }
}
@media(max-width:800px){
  .offers{
    margin-bottom: 60px;
  }
  .offers-left h1{
    font-size: 22px;
  }
  .offers-left p{
    font-size: 13px;
  }
  .offers-right img{
    width: 180px;
  }
  .offers-left button{
    width: 150px;
    height: 30px;
    font-size: 12px;
  }
}
@media(max-width:500px){
  .offers{
    width: 90%;
    margin: 0 auto;
    padding: 10px;
    background: #353030;
    margin-bottom: 50px;
  }
  .offers-left h1{
    font-size: 16px;
  }
  .offers-left p{
    font-size: 10px;
  }
  .offers-right img{
    width: 120px;
  }
  .offers-left button{
    width: 120px;
    height: 25px;
    font-size: 10px;
    margin-top: 12px;
  }
}


