.hero{
  /* width: 80%; */
  height: 100vh;
  /* background: linear-gradient(180deg, #fde1ff, #0ae04a22 60%); */
  display: flex;
  background: linear-gradient(120deg, #d70fe6, #24456bb6 0%);
}
.hero-left{
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 180px;
  gap: 20px;
  line-height: 1.1;
}

.hero-left h2{
  /* color: #090909; */
  color: white;
  font-size: 26px;
  font-weight: 700;
}
.hero-left p{
  /* color: #171717; */
  color: rgb(189, 209, 211);
  font-size: 100px;
  font-weight: 700;
}

.hero-hand-icon{
  display: flex;
  align-items: center;
  gap: 20px;
}
.hero-hand-icon img{
  width: 105px;
}

.hero-latest-btn{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  width: 310px;
  height:70px;
  border-radius: 75px;
  background: #ff4141; 
  margin-top: 30px;
  color: white;
  font-size: 22px;
  font-weight: 500;
  cursor: pointer;
}

.hero-right{
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero-right img{
  /* width: 520px; */
  width: 450px;
}

@media(max-width:1280px){
  .hero-left{
    padding-left: 100px;
  }
  .hero-left h2{
    font-size: 22px;
  }
  .hero-left p{
    font-size: 60px;
  }
  .hero-hand-icon img{
    width: 80px;
  }
  .hero-latest-btn{
    gap: 10px;
    width: 250px;
    height: 60px;
    margin-top: 20px;
    font-size: 18px;
  }
  .hero-right img{
    width: 500px;
  }
}
@media(max-width:1024px){
  .hero-left{
    padding-left: 80px;
  }
  .hero-left h2{
    font-size: 20px;
  }
  .hero-left p{
    font-size: 50px;
  }
  .hero-hand-icon img{
    width: 65px;
  }
  .hero-latest-btn{
    width: 220px;
    height: 50px;
    font-size: 16px;
  }
  .hero-right img{
    width: 400px;
  }
}
@media(max-width:800px){
  .hero-left{
    padding-left: 30px;
  }
  .hero-left h2{
    font-size: 16px;
  }
  .hero-left p{
    font-size: 40px;
  }
  .hero-hand-icon img{
    width: 50px;
  }
  .hero-latest-btn{
    width: 175px;
    height: 40px;
    font-size: 13px;
  }
  .hero-right img{
    width: 300px;
  }
}
@media(max-width:500px){
  .hero{
    /* height: 70vh; */
    flex-direction: column;
  }
  .hero-right{
    display: none;
  }
  .hero-left h2{
    font-size: 18px;
  }
  .hero-left p{
    font-size: 40px;
    line-height: 70px;
  }
  .hero-hand-icon img{
    width: 55px;
  }
  .hero-latest-btn{
    width: 200px;
    height: 45px;
    font-size: 14px;
  }
  .hero-right img{
    width: 300px;
  }
}
