.loginsignup{
  width: 100%;
  height: 80vh;
  background: #fce3fe;
  /* padding-top: 100px; */
  padding-top: 50px;
}
.loginsignup-container{
  width: 580px;
  /* height: 600px; */
  height: 430px;
  background: white;
  margin: auto;
  /* padding: 40px 60px; */
  padding: 15px 40px;
}
.loginsignup-container h1{
  /* margin: 200px 0px; */
}
.loginsignup-fields{
  display: flex;
  flex-direction: column;
  /* gap: 29px; */
  gap: 15px;
  /* margin-top: 30px; */
  margin-top: 15px;
}
.loginsignup-fields input{
  /* height: 72px; */
  height: 50px;
  width: 99%;
  /* padding-left: 20px; */
  border: 1px solid #c9c9c9;
  outline: none;
  color: #c5c5c5;
  font-size: 18px;
}
.loginsignup-container button{
  width: 580px;
  /* height: 72px; */
  height: 50px;
  color: white;
  background: #ff4141;
  margin-top: 15px;
  border: none;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
}
.loginsignup-login{
  margin-top: 16px;
  color: #5c5c5c;
  font-size: 18px;
  font-weight: 500;
}
.loginsignup-login span{
  color: #ff4141;
  font-weight: 600;
}
.loginsignup-agree{
  display: flex;
  margin-top: 10px;
  align-items: center;
  gap: 20px;
  color: #5c5c5c;
  font-size: 18px;
  font-weight: 500;
}

@media(max-width:1280px){
  .loginsignup{
    padding-top: 50px;
  }
  .loginsignup-container{
    width: auto;
    /* max-height: 700px; */
    height: 500px;
    max-width: 500px;
  }
  .loginsignup-container h1{
    margin: 10px 0px;
  }
  .loginsignup-fields{
    gap: 20px;
    margin-top: 20px;
  }
  .loginsignup-fields input{
    height: 65px;
    /* width: 93%; */
    width: 99%;
  }
  .loginsignup-container button{
    width: 100%;
  }
  .loginsignup-login{
    font-size: 16px;
  }
  .loginsignup-agree{
    font-size: 14px;
    gap: 10px;
  }
}
@media(max-width:800px){
  .loginsignup{
    height: 70vh;
    padding: 0;
  }
  .loginsignup-container{
    padding: 20px 30px;
  }
  .loginsignup-container h1{
    font-size: 24px;
  }
  .loginsignup-fields{
    margin-top: 20px;
  }
  .loginsignup-fields input{
    height: 50px;
  }
  .loginsignup-container button{
    height: 50px;
    font-size: 16px;
    
  }
}