.productdisplay{
  display: flex;
  /* margin: 0px 170px; */
  margin: 0px 50px;

}
.productdisplay-left{
  display: flex;
  /* align-items: center; */
  gap: 17px;
}
.productdisplay-img-list{
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.productdisplay-img-list img{
  height: 125px;
}
.productdisplay-main-img{
  width: 470px;
  height: 550px;
}
.productdisplay-right{
  /* background-color: red; */
  padding: 0px 70px;
  display: flex;
  flex-direction: column;
}
.productdisplay-right h1{
  color: #3d3d3d;
  font-size: 30px;
  font-weight: 700;
}
.productdisplay-right-stars{
  display: flex;
  margin-top: 13px;
  gap: 5px;
  color: #3d3d3d;
  align-items: center;
  font-size: 16px;
}
.productdisplay-right-prices{
  display: flex;
  gap: 30px;
  margin: 16px 0;
  font: 24px;
  font-weight: 700;
}

.productdisplay-right-price-old{
  text-decoration: line-through;
  color: #818181;
}
.productdisplay-right-price-new{
  color: #ff4141;
}
.productdisplay-right-size h1{
  margin-top: 10px;
  color: #656565;
  font-size: 20px;
  font-weight: 700;
}
.productdisplay-right-sizes{
  display: flex;
  margin: 16px 0;
  gap: 20px;
}
.productdisplay-right-sizes div{
  padding: 18px 24px;
  background: #fbfbfb;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  cursor: pointer;
}
.productdisplay-right button{
  padding: 20px 40px;
  width: 200px;
  font-size: 16px;
  font-weight: 600;
  color: white;
  background: #ff4141;
  margin-bottom: 10px;
  outline: none;
  border: none;
  cursor: pointer;
}
.productdisplay-right-category{
  margin-top: 10px;
}
.productdisplay-right-category span{
  font-weight: 600;
}
 @media(max-width:1280px){
  .productdisplay{
    margin: 0px 60px;
  }
  .productdisplay-left{
    gap: 20px;
  }
  .productdisplay-img-list{
    gap: 10px;
  }
  .productdisplay-img-list img{
    height: 120px;
  }
  .productdisplay-main-img{
    width: auto;
    height: 510px;
  }
  .productdisplay-right{
    padding: 0px 30px;
  }
  .productdisplay-right h1{
    font-size: 22px;
  }
  .productdisplay-right-stars{
    gap: 3px;
    font-size: 13px;
  }
  .productdisplay-right-prices{
    margin: 10px 0px;
    font-size: 18px;
  }
  .productdisplay-right-description{
    font-size: 13px;
  }
  .productdisplay-right-size h1{
    margin: 20px;
    font-size: 20px;
  }
  .productdisplay-right-sizes div{
    padding: 14px 20px;
  }
  .productdisplay-right button{
    width: 150px;
    padding: 15px;
    margin-bottom: 20px;
  }
  .productdisplay-right-category{
    margin-top: 5px; 
    font-size: 14px;
  }
}
@media(max-width:1024px){
  .productdisplay{
    margin: 0px 30px;
  }
  .productdisplay-left{
    gap: 5px;
  }
  .productdisplay-img-list img{
    height: 80px;
  }
  .productdisplay-main-img{
    height: 350px;
  }
  .productdisplay-right h1{
    font-size: 18px;
  }
  .productdisplay-right-stars img{
    width: 15px;
  }
  .productdisplay-right-prices{
    margin: 10px 0px;
    font-size: 18px;
  }
  .productdisplay-right-description{
    font-size: 13px;
  }
  .productdisplay-right-size h1{
    margin: 20px;
    font-size: 20px;
  }
  .productdisplay-right-sizes div{
    padding: 14px 20px;
  }
  .productdisplay-right button{
    width: 150px;
    padding: 15px;
    margin-bottom: 20px;
  }
  .productdisplay-right-category{
    margin-top: 5px; 
    font-size: 14px;
  }
}  
@media(max-width:800px){
  .productdisplay{
    margin: 0px 10px;
  }
  .productdisplay-left{
    gap: 0px;
    flex-direction: column-reverse;
    align-items: center;
  }
  .productdisplay-img-list{
    flex-direction: row;
    gap: 6px;
  }
  .productdisplay-img-list img{
    height: 70px;
  }
  .productdisplay-main-img{
    height: 300px;
  }
  .productdisplay-right h1{
    font-size: 14px;
  }
  .productdisplay-right-stars img{
    width: 13px;
  }
  .productdisplay-right-description{
    font-size: 12px;
  }
  .productdisplay-right-size h1{
    margin-top: 10px;
  }
  .productdisplay-right-sizes {
    margin-top: 10px;
    gap: 5px;
  }
  .productdisplay-right-sizes div{
    padding: 4px 11px;
  }
  .productdisplay-right button{
    width: 100px;
    padding: 12px;
  }
  .productdisplay-right-category{
    font-size: 12px;
  }
}
@media(max-width:500px){
  .productdisplay{
    flex-direction: column;
  }
  .productdisplay-left{
    width: 100%;
    gap: 10px;
    flex-direction: row;
    /* margin: auto; */
    justify-content: center;
    /* align-items: center; */
    /* background: red; */
    /* height: 70vh; */
    
  }
  .productdisplay-img-list{
    flex-direction: column;
    gap: 8px;
  }
  .productdisplay-img-list img{
    /* height: 75px; */
    width: 60px; 
    height: 74px;
  }
  .productdisplay-main-img{
    display: flex;
    width: 210px; /* Optional: Adjust the size of the main image */
    height: 320px;
  }
  .productdisplay-right{
    margin: 5px;
  }
  .productdisplay-right h1{
    margin: 5px;
    font: 20px;
    font-weight: 500;
  }
  .productdisplay-right-stars img{
    width: 15px;
  }
  .productdisplay-right-sizes {
    margin-top: 20px 0px;
    gap: 10px;
  }
  .productdisplay-right-sizes div{
    padding: 8px 11px;
    font-size: 14px;
  }
  .productdisplay-right button{
    width: 130px;
    font-size: 15px;
    padding: 12px 0px;
  }
  .productdisplay-right-category{
    font-size: 16px;
  }
}  